import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DatabaseProviderService } from '../services/database-provider.service';
import { Router } from "@angular/router";

@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.page.html',
  styleUrls: ['./add-product.page.scss'],
})
export class AddProductPage implements OnInit {

  addProductForm: FormGroup;
  constructor(
    private _DB: DatabaseProviderService,
    private _FB: FormBuilder, 
    private _router: Router
  ) { }

  ngOnInit() {
    this.addProductForm = this._FB.group({
      product_name: ['', Validators.required],
      product_quantity: ['', Validators.minLength(1)],
      product_price: ['', Validators.minLength(2)],
      product_tax: ['', Validators.minLength(1)]
    })
  }

  onAddProduct() {
    if(this.addProductForm.valid) {
      console.log("Form page: ",this.addProductForm.value);
      let data = this.addProductForm.value;
      this._DB.addProduct(data.product_name, data.product_quantity, data.product_price, data.product_tax)
        .then(() => {
          console.log("One Product Row Created and i.e ",data)
          this._DB.retrieveProductRecords()
            .then(() => {
              console.log("Data has Retrieved")
              this._DB.exportAsJSON()
            }).then((data) => console.log("Exported in JSON: ", data))
        }).then(() => {
          this.addProductForm.reset();
          this._router.navigate(['/home'])
        }).catch(e => console.log(e));
    }
  } 

}
